// SECTION
.module__section {

    font-size: 1.2rem;
    line-height: 1em;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-left: 9rem;
    position: relative;
    margin-bottom: 3rem;
    @media ( min-width: 992px ) { margin-bottom: 5rem; }

    span {
        opacity: 0;
        transition: all .3s .3s;
    }

    &:before {
        position: absolute;
        top: 50%;
        left: 0;
        content: '';
        display: inline-block;
        width: 0;
        height: 1px;
        background: $color_white;
        transition: all .3s;
        transform: translateY(-50%);
    }

}

.animation {
    .module__section span {
        opacity: 1 !important;
    }
    .module__section:before {
        width: 53px !important;
    }
}

// TITLE {
.module__title {
    font-family: $font_heading;
    line-height: .9;
    font-size: 3.6rem;
    font-weight: 300;
    margin-bottom: 2rem;
    @media ( min-width: 768px ) {
        font-size: 5rem;
    }
    @media ( min-width: 992px ) {
        font-size: 6rem;
        margin-bottom: 4rem;
    }
    @media ( min-width: 1200px ) {
        font-size: 8rem;
    }
    span span {
        line-height: .9em !important;
    }
}

// SUBTITLE
.module__subtitle {
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: 2rem;
    @media ( min-width: 768px ) {
        font-size: 2.4rem;
    }
    @media ( min-width: 992px ) {
        margin-bottom: 4rem;
    }
    @media ( min-width: 1200px ) {
        font-size: 3rem;
    }
}


// PRECONTENT
.module__precontent {

    margin-top: 0;
    margin-bottom: 3rem;
    @media ( min-width: 992px ) {
        margin-top: 7rem;
        margin-bottom: 4.5rem;
    }

    p {
        font-size: 3rem !important;
        line-height: 1.2em !important;
    }

}

// CONTENT
.module__content {
    margin-top: 3rem;
    @media ( min-width: 992px ) {
        margin: 0;
    }
}